  font-family: Arial, Helvetica, sans-serif;
  font-family: 400;
  margin: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

@media screen and (width >= 480px) {
  .container {
    outline: 1px solid #000;
    width: 480px;
  }
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
  }
}

#search-input {
  width: 300px;
  height: 50px;
  margin: 0 auto;
  padding-left: 10px;
  display: block;
}

.load-more {
  color: #fff;
  background-color: #565656;
  border: 1px solid #2c2c2c;
  border-radius: 10px;
  width: 150px;
  height: 50px;
  margin: 0 auto;
  font-size: 16px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin: 0;
